import requestHttps from '../../common/request.js'
export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			navData:[
				{id:'0','mobile_name':'淘宝'},
				// {id:'0','mobile_name':'京东'},
				// {id:'1','mobile_name':'拼多多'},
				// {id:'2','mobile_name':'知了优选'},
				// {id:'3','mobile_name':'苏宁'}
			],
			current:0,
			navScrollLeft:0,
			keyword:'',
			windowWidth: 400,
			sercolse:true,
			hotlist:[],
			address:'',
			address_id:'',
			selist:[],
		}
	},
	methods: {
		// 切换
		swictNav:function(e){
			// console.log(e)
			var that=this
			var cur=e.currentTarget.dataset.curtab
			var id=e.currentTarget.dataset.id
			// navScrollLeft
			var singleNavWidth = that.windowWidth / 4
			    //tab选项居中                            
			that.navScrollLeft=(cur - 2) * singleNavWidth
			if(that.current==cur){
				return false
			}else{
				 that.current=cur
				 that.id=id
			} 
			// that.current=cur
		},
		// 搜索文字
		bindinput:function(e){
			console.log(e)
			var that=this
			var val=e.detail.value
			 
			that.keyword=val
			if(val){
				that.getsuggestlist(val)
			}else{
				that.selist=[]
			}
			console.log(that.keyword)
		},
		// 搜索框失去焦点
		bindblur:function(e){
			var that=this
			console.log(e)
			that.sercolse=true
		},
		// 聚焦时
		bindfocus:function(e){
			var that=this
			var secols = true
			console.log(e)
			var val = e.detail.value
			if (val.length > 0 || that.keyword != '' || that.keyword != undefined) {
			  secols = false
			}
			that.sercolse=secols  
		},
		// 热门关键字到详情
		gohotkey:function(e){
			var ks=e.currentTarget.dataset.keyword
			uni.navigateTo({
				url: '../searchlist/searchlist?kw='+ks,
				success: res => {
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		goseli:function(e){
			var kw=e.currentTarget.dataset.name
			uni.navigateTo({
				url: '../searchlist/searchlist?kw='+kw,
				success: res => {
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 确认
		bindconfirm:function(e){
			var that =this
			if(that.keyword){
				that.clicksear()
			}
		},
		colsekeywd:function(){
			var that=this
			that.keyword=''
			that.sercolse=true
			console.log(that.keyword)
			console.log('that.keyword')
		},
		// 确定搜索
		clicksear:function(e){
			var that=this
			var keyword=that.keyword
			if(keyword){
				uni.navigateTo({
					url: '../searchlist/searchlist?kw='+keyword,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			
		},
		getsuggestlist:function(kw){
			var that=this
			if(kw){
				requestHttps({
					url:'/tb/suggestkw/lists',
					method:'POST',
					data:{
						kw:kw
					},
					success: res=>{
						var list=res.data.data
						that.selist=list
						console.log(list)
					}
				})
			}
			
		},
		// 搜索关键字推荐
		getcommlist:function(){
			var that =this
			uni.request({
				url: '',
				method: 'GET',
				data: {},
				success: res => {},
				
			});
			requestHttps({
				url:'/tb/keyword/lists',
				method:'POST',
				data:{},
				success: res=>{
					var list=res.data.data
					that.hotlist=list
				},
				fail: (res) => {
					console.log('失败')
					console.log(res)
				},
				 
				 
			})
		}
	},
	onLoad:function(){
		var that=this
		that.getcommlist()
	},
	onShow:function(){
		 
	}
}
